.about{
    background: #FFf;
}

.about__text{
    text-align: 'justify';
}

#container-content{
    background: rgba(75,5,62,0.15);
background: linear-gradient(90deg,rgba(75,5,62,0.15) 0%, rgba(139,6,6,0.52) 80%);
background: -webkit-linear-gradient(90deg,rgba(75,5,62,0.15) 0%, rgba(139,6,6,0.52) 80%);
background: -moz-linear-gradient(90deg,rgba(75,5,62,0.15) 0%, rgba(139,6,6,0.52) 80%);
    border-radius: 10px;
}
img.about__img{
    border-radius: 10px;
    width: 65%;
    margin:auto;
    display: flex;
    text-align: center;
}

h1.about__heading{
    margin-top: 10px;
    margin-bottom: 10px;
    font-weight: 700;
    text-align: start;
    font-family: Montserrat,sans-serif;
    font-size: 40px;
}
button.about.btn.pointer {
    text-transform: capitalize;
    width: 195px;
    height: 55px;
    font-size: 18px;
    transition: .4s ease;
}
.about__button{
    padding: 20px 0px;
}
a.bottom__to__top{
    height: 40px;
    width: 40px;
    position: fixed;
    bottom: 3%;
    right: 2%;
    background-color: #f00000;
    border-radius: 50%;
    cursor: pointer;
    display: none;
    align-items: center;
    justify-content: center;
    transition: .4s all ease;
    z-index: 1000;
}
a.bottom__to__top.active{
    display: flex;
}
/* RESPONSIVE */
@media only screen and (max-width: 1024px){
    .p__color{
        font-size: 15px!important;
    }
}
@media only screen and (max-width: 425px){
button.about.btn.pointer {
    text-transform: capitalize;
    width: 165px;
    height: 50px;
    font-size: 13px;
    transition: .4s ease;
}
}